.card {
  box-sizing: border-box;
  width: 200px;
  padding: 32px 16px 16px;
  background: $white;
  border: 1px solid $gray-elements;
  border-radius: 5px;
  transition: 300ms ease-in-out;

  &:hover {
    transform: scale(1.2);

    .card__title {
      color: $dark-blue-accent;
    }
  }

  &__image {
    display: block;
    margin: 0 auto 40px;
    height: 134px;
    width: 160px;
  }

  &__title {
    margin: 0 0 4px;
    line-height: 18px;
    font-size: 12px;
    font-weight: 500;
    color: $main-accent;
    transition: color 300ms ease-in-out;
  }

  &__code {
    display: block;
    font-size: 10px;
    margin-bottom: 16px;
    color: $main-secondary;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;

    &-count {
      font-size: 10px;
      line-height: 14px;
      text-align: right;
    }
  }

  &__price-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price-label {
    color: $main-secondary;
    font-size: 12px;
    line-height: 18px;
  }

  &__price {
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
    color: $main-accent;
  }

  &__buy {
    background-color: $blue-accent;
    text-decoration: none;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    height: 40px;
    font-size: 14px;
    line-height: 16px;
    color: $white;
    text-transform: uppercase;
    font-weight: 700;
    transition-duration: 400ms;
    transition-property: color,background-color;
    transition-timing-function: ease-in-out;
  }

  &__buy:hover {
    border: 1px solid $blue-accent;
    color: $blue-accent;
    background-color: $white;
  }

}
